<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"  class="shezhi">
      <van-cell v-for="item in list" :key="item.art_id.toString()">
        <div slot="title">
          <div>{{item.title}}</div>
          <lazy-component>
            <van-image
              width="110px"
              height="74px"
              fit="contain"
              :src="dz"
              lazy-load
              v-for="(dz,i) in item.cover.images"
              :key="i"
            />
          </lazy-component>
        </div>
        <div slot="label" class="miaoshu">
          <span>{{item.aut_name}}</span>
          <span>{{item.comm_count}}评论</span>
          <span>{{item.pubdate}}</span>
        </div>
      </van-cell>
    </van-list>
  </van-pull-refresh>
</template>

<script>
import { pindaoNews } from '@/api/new.js'
export default {
  data () {
    return {
      list2: [],
      list: [],
      loading: false,
      finished: false,
      isLoading: false,
      pre_timestamp: null
    }
  },
  props: ['pdid'],
  methods: {
    // 获取内容
    async getnews () {
      let timestamp = this.pre_timestamp ? this.pre_timestamp : Date.now()
      // debugger
      let res = await pindaoNews({
        channel_id: this.pdid,
        with_top: 1,
        timestamp
      })
      return res
    },
    // 下拉刷新
    async onRefresh () {
      this.pre_timestamp = Date.now()
      let res = await this.getnews()
      this.list.unshift(...res.data.results)
      this.pre_timestamp = res.data.pre_timestamp
      this.isLoading = false
    },
    async onLoad () {
      // 异步更新数据
      let res = await this.getnews()
      console.log(res.data.results)

      this.list.push(...res.data.results)
      this.pre_timestamp = res.data.pre_timestamp
      // 加载状态结束
      this.loading = false

      // 数据全部加载完成
      if (res.data.results.length === 0) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang='less' scoped>
.shezhi{
  padding-bottom: 55px;
}
.miaoshu {
  span {
    margin-left: 8px;
  }
  span:nth-child(1) {
    margin-left: 0px;
  }
}
</style>
