<template>
  <div>
    <van-sticky>
      <van-nav-bar @click-right="sousuo">
        <div slot="left" class="logo"></div>
        <span slot="right">
          <van-button round type="info" size="small" @click="$router.push('/search')">
            <i class="iconfont icon-sousuo"></i>圆形按钮
          </van-button>
        </span>
      </van-nav-bar>
    </van-sticky>
    <!-- 频道栏 -->
    <van-tabs v-model="active" sticky :offset-top="46">
      <span slot="nav-left" class="map">
        <van-icon name="wap-nav" @click="show = true;" />
      </span>
      <van-tab :title="pd.name" v-for="pd in pdlist" :key="pd.id">
        <liebiao :pdid="pd.id"></liebiao>
      </van-tab>
    </van-tabs>
    <!-- 弹出层 -->
    <van-popup
      closeable
      round
      close-icon-position="top-left"
      v-model="show"
      position="bottom"
      :style="{ height: '95%' }"
      @close="popupgb"
    >
      <van-row type="flex" justify="center" align="center" class="bjpd">编辑频道</van-row>
      <van-cell class="cell">
        <!-- 使用 title 插槽来自定义标题 -->
        <template slot="title">
          <span class="custom-title">我的频道</span>
          <span class="djpd">点击{{editpd?"删除":"进入"}}频道</span>
        </template>
        <!-- <button slot="right-icon">111</button> -->

        <van-button
          type="danger"
          slot="right-icon"
          size="mini"
          round
          class="anniu"
          @click="editpd=!editpd"
        >{{editpd?"完成":"编辑"}}</van-button>
      </van-cell>
      <!-- 我的频道 -->
      <van-grid gutter="10" :square="false">
        <van-grid-item
          class="fu"
          v-for="(pd,index) in pdlist"
          :key="pd.id"
          @click="pddianji(index)"
        >
          <van-icon name="close" slot="icon" class="shanchu" v-show="editpd&& index!=0" />
          <span slot="text" class="pindao" :class="{active:index===active}">{{pd.name}}</span>
        </van-grid-item>
      </van-grid>
      <!-- 所有频道 -->
      <van-cell class="cell">
        <!-- 使用 title 插槽来自定义标题 -->
        <template slot="title">
          <span class="custom-title">推荐频道</span>
          <span class="djpd">点击添加频道</span>
        </template>
        <!-- <button slot="right-icon">111</button> -->
      </van-cell>
      <van-grid gutter="10" :square="false">
        <van-grid-item
          class="fu"
          v-for="(pd,index) in shengyupd"
          :key="pd.id"
          @click="sypddianji(index)"
        >
          <!-- <van-icon name="close" slot="icon" class="shanchu" /> -->
          <span slot="text" class="pindao">{{pd.name}}</span>
        </van-grid-item>
      </van-grid>
    </van-popup>
  </div>
</template>

<script>
import liebiao from '@/components/home/liebiao'
import { pindao, allpds } from '@/api/new'
import { resetpd } from '@/api/user'
import { getItem } from '@/utils/storage'
export default {
  data () {
    return {
      editpd: false,
      show: false,
      active: 0,
      pdlist: [],
      allpd: []
    }
  },
  computed: {
    shengyupd () {
      let newall = this.allpd.filter(item => {
        // debugger
        let a = this.pdlist.some(ite => {
          return ite.name === item.name
        })
        if (!a) {
          return item
        }
      })
      return newall
    }
  },
  watch: {
    pdlist () {
      if (this.$store.state.token) {
        this.setpd()
      } else {
        window.localStorage.setItem('list', JSON.stringify(this.pdlist))
      }
    }
  },
  methods: {
    // 发送保存频道liebiao
    async setpd () {
      let res = await resetpd({
        channels: this.pdlist.map((item, index) => {
          return { id: item.id.toString(), seq: index.toString() }
        })
      })
      console.log(res)
    },
    // 剩余频道添加
    sypddianji (index) {
      this.pdlist.push(this.shengyupd[index])
    },
    // 频道点击
    pddianji (index) {
      if (this.editpd && index !== 0) {
        this.pdlist.splice(index, 1)
        console.log('删除了')
      } else {
        console.log('要跳转')
        this.show = false
        this.active = index
      }
    },
    // 关闭弹出层
    popupgb () {
      this.editpd = false
    },
    sousuo () {
      console.log('搜索11111111')
    }
  },
  async created () {
    if (this.$store.state.token) {
      let res = await pindao()
      console.log(res.data)

      this.pdlist = res.data.channels
    } else {
      let pdlist = getItem('list')
      if (pdlist) {
        this.pdlist = pdlist
      } else {
        let res = await pindao()
        console.log('获取频道', res.data)

        this.pdlist = res.data.channels
      }
    }

    // console.log('频道list', pdlist)

    let allpd = await allpds()

    this.allpd = allpd.data.channels
  },

  components: {
    liebiao
  }
}
</script>

<style lang='less' scoped>
.active {
  color: orange;
}

::v-deep .van-grid-item__content {
  background: greenyellow;
  padding: 6px 0;
  position: relative;
  .shanchu {
    position: absolute;
    top: -10px;
    right: -46px;
    font-size: 18px;
  }
}
.pindao {
  font-size: 16px;
}
.cell {
  .anniu {
    width: 60px;
    height: 25px;
  }
}
.djpd {
  font-size: 12px;
  color: #ccc;
  margin-left: 10px;
}
.bjpd {
  font-size: 16px;
  height: 40px;
}
.van-tabs__nav {
  position: relative;
  .map {
    position: fixed;
    top: 48px;
    right: 0;
    background-color: #ffffff;
    z-index: 19;
  }
}

.logo {
  width: 100px;
  height: 46px;
  background-image: url("../../assets/img/logo-light.png");
  background-size: 100px;
  background-position: center;
  background-repeat: no-repeat;
}

.van-button {
  background-color: red;
  width: 175px;
}
</style>
